import React, { useEffect, useRef } from "react";
import { Rect, Transformer } from "react-konva";
import Konva from "konva";

export interface RectProps {
  x: number;
  y: number;
  width: number;
  height: number;
  id: string;
  stroke: string;
  label: string;
}

export interface BBoxProps {
  rectProps: RectProps;
  onChange: (newRect: RectProps) => void;
  isSelected: boolean;
  onClick: () => void;
  scale: number;
  strokeWidth: number;
  mode?: string;
}

const BBox: React.FC<BBoxProps> = ({
  rectProps,
  onChange,
  isSelected,
  onClick,
  scale,
  strokeWidth,
  mode = "Edit",
}) => {
  // Ref  Konva.Rect Konva.Transformer  
  const shapeRef = useRef<Konva.Rect | null>(null);
  const trRef = useRef<Konva.Transformer | null>(null);

  useEffect(() => {
    if (isSelected && trRef.current && shapeRef.current) {
      // Transformer 
      trRef.current.nodes([shapeRef.current]);
      
      //   
      trRef.current.visible(true);
      
      //      
      trRef.current.enabledAnchors([
        'top-left', 'top-center', 'top-right', 
        'middle-left', 'middle-right', 
        'bottom-left', 'bottom-center', 'bottom-right'
      ]);

      //    ( )
      trRef.current.keepRatio(false);

      //   (  )
      trRef.current.rotateEnabled(false);

      //      
      trRef.current.padding(2);

      //   
      trRef.current.anchorSize(10);

      //    
      trRef.current.anchorStrokeWidth(2);

      //   
      trRef.current.anchorFill('#FFFFFF');
      trRef.current.anchorStroke(rectProps.stroke || '#0000FF');
      
      //    
      trRef.current.anchorCornerRadius(4);

      //   (:   )
      trRef.current.getLayer()?.batchDraw();
    }
  }, [isSelected, rectProps.stroke]);

  // Edit     
  const isDraggable = mode === "Edit";

  return (
    <>
      <Rect
        ref={shapeRef}
        x={rectProps.x * scale}
        y={rectProps.y * scale}
        width={rectProps.width * scale}
        height={rectProps.height * scale}
        stroke={rectProps.stroke}
        strokeWidth={strokeWidth}
        dash={[5, 5]} //   
        fill={isSelected ? rectProps.stroke + "20" : "transparent"} //      
        draggable={isDraggable} //    
        onClick={onClick} //   
        onTap={onClick} //    
        //     
        onDragEnd={(e) => { 
          if (!shapeRef.current) return;
          const node = shapeRef.current;
          onChange({ 
            ...rectProps,
            x: node.x() / scale,
            y: node.y() / scale,
          });
        }}
        //       
        onTransformEnd={(e) => {
          if (!shapeRef.current) return;
          const node = shapeRef.current;
          const scaleX = node.scaleX();
          const scaleY = node.scaleY();

          //  
          node.scaleX(1);
          node.scaleY(1);

          //     
          onChange({
            ...rectProps,
            x: node.x() / scale,
            y: node.y() / scale,
            width: Math.max(5, node.width() * scaleX) / scale, //    
            height: Math.max(5, node.height() * scaleY) / scale, //    
          });
        }}
      />
      {isSelected && mode === "Edit" && (
        <Transformer 
          ref={trRef}
          anchorStroke={rectProps.stroke || '#0000FF'}
          anchorFill="#FFFFFF"
          anchorSize={10}
          anchorCornerRadius={4}
          borderStroke={rectProps.stroke || '#0000FF'}
          borderDash={[3, 3]}
          borderStrokeWidth={1}
          boundBoxFunc={(oldBox, newBox) => {
            //    (5x5 )
            if (newBox.width < 5 || newBox.height < 5) {
              return oldBox;
            }
            return newBox;
          }}
        />
      )}
    </>
  );
};

export default BBox;